import React from 'react'
import Helmet from 'react-helmet'

import Layout from '../components/layout'
// import Lightbox from 'react-images'
import Gallery from '../components/Gallery'

import thumb01 from '../assets/images/thumbs/coffeerun.png'
import thumb02 from '../assets/images/thumbs/kubbar.png'
import thumb03 from '../assets/images/thumbs/weatherjukebox.png'
import thumb04 from '../assets/images/thumbs/podtalk.png'
import thumb05 from '../assets/images/thumbs/simplyconnect.png'
import thumb06 from '../assets/images/thumbs/06.jpg'

import full01 from '../assets/images/fulls/coffeerun.png'
import full02 from '../assets/images/fulls/kubbar.png'
import full03 from '../assets/images/fulls/weatherjukebox.png'
import full04 from '../assets/images/fulls/podtalk.png'
import full05 from '../assets/images/fulls/simplyconnect.png'
import full06 from '../assets/images/fulls/06.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, link: 'https://github.com/adigladi/CoffeeRun', caption: 'Coffee Run', description: 'Webapp using Google Maps API and firebase.' },
    { id: '2', src: full02, thumbnail: thumb02, link: 'https://github.com/adigladi/KubbAR', caption: 'KubbAR', description: 'Android app built in Unity with AR Core.' },
    { id: '3', src: full03, thumbnail: thumb03, link: 'https://github.com/adigladi/weatherjukebox', caption: 'Weather Jukebox', description: 'Webapp playing music matching the weather.' },
    { id: '4', src: full04, thumbnail: thumb04, link: 'https://github.com/benforsrup/RNPodcastApp-KTH', caption: 'PodTalk', description: 'React native app bringing social media to Podcasts.' },
    { id: '5', src: full05, thumbnail: thumb05, caption: 'Simply Connect', description: 'Social media prototype designed for elders.' }
];

class HomeIndex extends React.Component {

    constructor() {
        super();

        this.state = {
            lightboxIsOpen: false,
            currentImage: 0,
        };

        this.closeLightbox = this.closeLightbox.bind(this);
        this.gotoNext = this.gotoNext.bind(this);
        this.gotoPrevious = this.gotoPrevious.bind(this);
        this.openLightbox = this.openLightbox.bind(this);
        this.handleClickImage = this.handleClickImage.bind(this);
    }

    openLightbox(index, event) {
        event.preventDefault();
        this.setState({
            currentImage: index,
            lightboxIsOpen: true,
        });
    }
    closeLightbox() {
        this.setState({
            currentImage: 0,
            lightboxIsOpen: false,
        });
    }
    gotoPrevious() {
        this.setState({
            currentImage: this.state.currentImage - 1,
        });
    }
    gotoNext() {
        this.setState({
            currentImage: this.state.currentImage + 1,
        });
    }
    handleClickImage() {
        if (this.state.currentImage === this.props.images.length - 1) return;

        this.gotoNext();
    }

    render() {
        const siteTitle = "Adrian Berger - Portfolio"
        const siteDescription = "Portfolio"

        return (
            <Layout>
                <Helmet>
                    <title>{siteTitle}</title>
                    <meta name="description" content={siteDescription} />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header className="major">
                            <h2>Let me tell you about myself.</h2>
                        </header>
                        <p>I'm a Swedish student currently working on my masters degree in Interactive Media Technology at KTH - Royal Institute of Technology.</p>
                    </section>

                    <section id="two">
                        <h2>My Work</h2>

                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, link, caption, description }) => ({
                            src,
                            thumbnail,
                            link,
                            caption,
                            description
                        }))} />

                        {/* <ul className="actions">
                            <li><a href="#" className="button">Full Portfolio</a></li>
                        </ul> */}
                    </section>

                    <section id="three">
                        <h2>Get In Touch</h2>
                        <div className="row">

                            <div className="4u 12u$(small)">
                                <ul className="labeled-icons">
                                    <li>
                                        <h3 className="icon fa-home"><span className="label">Address</span></h3>
                                        Stockholm, Sweden
                                    </li>
                                    <li>
                                        <h3 className="icon fa-mobile"><span className="label">Phone</span></h3>
                                        +46 76-780 64 99
                                    </li>
                                    <li>
                                        <h3 className="icon fa-envelope-o"><span className="label">Email</span></h3>
                                        <a href="mailto:adigladi@gmail.com">adigladi@gmail.com</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </section>

                </div>

            </Layout>
        )
    }
}

export default HomeIndex